@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

body::before {
    content: '';
    background: linear-gradient(180deg, #FFFFFF 0%, #F1F9FF 100%);
    @apply absolute top-0 left-0 w-full h-[100vh] -z-1
}

.header {
    @apply py-6 w-full z-50 relative -sm:py-4;
}

.container {
    @apply w-4/5 max-w-7xl mx-auto -xl:w-11/12 -sm:w-full -sm:px-4;
}

.nav-item {
    @apply mr-8 -xl:mr-4;
}

a {
    cursor: pointer !important;
}

.link {
    @apply font-bold text-color-dark leading-6 transition-colors -xl:text-sm
}

.selected-lang {
    @apply flex items-center font-bold ml-auto py-1.5 px-3 rounded-3xl cursor-pointer transition-all
}

.selected-lang:hover {
    @apply bg-color-200
}

.btn {
    @apply text-white font-bold py-6 px-8 rounded-full uppercase transition-all shadow-xl shadow-inherit text-sm border-none -sm:text-[10px] leading-[1.4em] -sm:py-5 -sm:px-6;
}

.btn.dark-mode {
    @apply text-color-dark
}

.btn-sm {
    @apply text-white py-3 px-6 rounded-full uppercase transition-all font-bold border-none text-xs;
}

.btn-border {
    transition: all .4s ease;
    @apply text-color-blue uppercase text-sm py-[23px] px-14 border border-color-blue rounded-[50px] font-semibold block w-max hover:bg-color-blue hover:text-white -sm:text-[10px] -sm:leading-[1.4em] -sm:py-5 
}

.btn-border-sm {
    transition: all .4s ease;
    @apply text-color-blue uppercase text-[12px] py-3 px-6 border border-color-blue rounded-[50px] font-semibold block w-max hover:bg-color-blue hover:text-white whitespace-nowrap -sm:py-[10px] -sm:px-4 -sm:text-[10px] -sm:leading-[1.5em]
}

.title-1 {
    @apply font-bold text-color-dark text-5xl uppercase -xl:text-4xl -sm:text-[2rem] -sm:leading-[1em]
}

.title-1.white {
    @apply text-white;
}

.title-2 {
    @apply font-bold text-color-dark text-4xl leading-[1em] -xl:text-3xl -sm:text-2xl -sm:leading-[1em];
}

.title-2.white,
.title-3.white {
    @apply text-white;
}

.title-3 {
    @apply font-bold text-3.5xl leading-[1.188em] -sm:text-lg -sm:leading-[1.222em]
}

.title-4 {
    @apply font-bold text-color-dark text-2xl -sm:text-sm -sm:leading-[1.214em];
}

.title-5 {
    @apply font-bold text-color-dark text-opacity-20 text-2xl;
}

.slide-1__info {
    @apply w-[45.15625%] -lg:w-full -lg:text-center -lg:flex -lg:flex-col -lg:items-center
}

.slide-1__gallery {
    @apply w-[48.75%] -lg:w-[70%] -lg:mx-auto -sm:w-[85%]
}

.img-bordered-block {
    box-shadow: inset 0 0 0 2px #DCA606;
    @apply  h-auto flex overflow-hidden before:block relative bg-color-gold bg-cover bg-no-repeat bg-center 
}

.img-bordered-block img {
    @apply w-full object-cover object-center
}

.cube {
    @apply w-full before:pb-[100%] before:block
}

.js-hero-slider .swiper-slide:not(.swiper-slide-active), 
.js-review-swiper .swiper-slide:not(.swiper-slide-active){
    opacity: 0 !important;
  }

.hero-swiper-pagination {
    width: max-content !important;
    bottom: 123px !important;
    left: 43px !important;
    @apply -md:flex -md:flex-col
}

.hero-swiper-pagination .swiper-pagination-bullet {
    margin: 0 8px !important;
}

.hero-swiper-pagination .swiper-pagination-bullet,
.js-review-slider-pagination .swiper-pagination-bullet {
    width: 5px;
    height: 5px;
}
 
.js-review-slider-pagination .swiper-pagination-bullet:not(:last-child) {
    margin: 0 16px 0 0 !important;
}

.hero-swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active,
.js-review-slider-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
    background: #FF4A35;
}

.js-review-slider-pagination {
    bottom: -34px !important;
    display: none;
}

.js-review-btn {
    box-shadow: 0px 15px 25px rgba(15, 144, 209, 0);
    @apply transition-all
}

.js-review-btn .review-user-btn__icon {
    transition: all 0.3s ease;
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
}

.js-review-btn:hover .review-user-btn__icon, 
