}

strong.program-card__oldprice span.currency {
    @apply text-[8px]
}

.program-card__likes {
    @apply text-base font-bold text-white absolute top-[23px] left-6 py-1 pl-3 pr-4 rounded-[10px] flex items-center  transition-all -sm:text-xs -sm:leading-[1.5em] -sm:rounded-[5px]
}

.program-card__likes img {
    @apply w-[20px] h-auto mr-2 -sm:w-4 -sm:h-auto -sm:mr-[6px]
}

.program-card__likes .extra {
    @apply text-xs font-normal ml-2 whitespace-nowrap hidden
}

.program-card__likes:hover .extra {
