}

.way-tabs-list {
    @apply flex items-center justify-center border-b-2 w-max mx-auto mb-[105px] -md:justify-start -md:w-full -md:max-w-full -md:mb-8 -md:flex-wrap
}

.way-tabs-list > div {
    @apply -md:w-1/2
}

.way-tab {
    @apply flex items-center justify-center min-w-[210px] relative py-4 cursor-pointer text-color-gray after:absolute after:bottom-0 after:left-0 after:h-[2px] after:w-full  after:transition-all after:bg-transparent transition-all -lg:text-sm -lg:min-w-[175px] -md:w-full -min:text-xs -min:min-w-0 -min:py-3
