.way-tabs-list > div:nth-child(3) .way-tab.active {
    @apply after:bg-color-orange
}

.way-tabs-list > div:nth-child(4) .way-tab.active svg path,
.way-tabs-list > div:nth-child(4) .way-tab:hover svg path {
    @apply fill-color-green
}

.way-tabs-list > div:nth-child(4) .way-tab.active {
    @apply after:bg-color-green
}

.way-wrapper .container {
    @apply max-w-[1062px] mx-auto flex items-center justify-between -md:flex-col
}

.way-title {
    @apply font-bold
}

.swiper.js-way-slider {
    @apply overflow-visible
} 

.success-list-wrapper {
    width: calc(100% - 328px);
    @apply pl-16 border-l-2 border-l-color-dark -lg:w-full -lg:pl-0 -lg:border-none
}

.success-list {
