}

.review-user-btn__icon {
    @apply relative border-[2px] after:block after:pb-[100%] flex overflow-hidden bg-cover bg-no-repeat bg-center
}

.news-list {
    grid-template-areas:
    "article-1 article-1 article-2 article-2"
    "article-1 article-1 article-3 article-4";
    @apply grid grid-cols-4 gap-8 -lg:grid-cols-1;
}

.news-item {
    @apply relative z-1 overflow-hidden rounded-[20px] p-8 min-h-[314px] -sm:p-6 -sm:min-h-[220px]
}

.news-item:first-child {
   grid-area: article-1;
   @apply -sm:min-h-[480px]
   
}
.news-item:nth-child(2) {
   grid-area: article-2;
}

.news-item:nth-child(3) {
