}

.copy aL

.policy-list {
    @apply flex items-center mr-8 -sm:flex-col -sm:items-center -sm:mr-0 -sm:mb-4
}

.policy-list__item {
    @apply -sm:text-center
}

.policy-list__item:not(:last-child) {
