}

.socials__link:hover svg path {
    @apply fill-color-blue
}

#map { 
    @apply h-[720px] -md:h-[100vw] 
}

.map-popup {
    @apply absolute right-[16.2vw] py-12 pl-12 pr-6 bg-white rounded-[20px] z-[9999999999] w-11/12 max-w-[360px] transition-all top-32 opacity-0 invisible -sm:right-auto -sm:left-1/2  -sm:-translate-x-1/2 -sm:fixed -sm:pl-6 -sm:pr-3
}

.map-popup.active {
    @apply top-[95px] opacity-100 visible -sm:top-8;
}

.map-popup__title {
    @apply font-bold text-color-dark text-2xl leading-[1.083em] mb-6
}

.map-popup__list {
    @apply max-h-[358px] overflow-y-auto pr-[22px]
}

.map-popup__close {
    @apply absolute top-4 right-4
}

.map-popup__item {
    @apply py-6 border-b border-b-[#DDEBF6]
}

.map-popup__item:first-child {
