}

.map-popup__item-place {
    @apply text-sm leading-[1.5em] text-color-dark mt-1
}

.dropdown select {
    display: none;
}

.dropdown {
    @apply relative tracking-wider uppercase
}

.dropdown.open > span,
.dropdown > span:hover {
    @apply bg-color-200
}

.c-dark .dropdown.open > span,
.c-dark .dropdown > span:hover {
    @apply bg-transparent
}

.dropdown > span {
    @apply flex items-center font-bold text-base ml-auto py-[3px] px-3 rounded-3xl cursor-pointer uppercase transition-all -sm:text-[10px];
}
.dropdown:not(.regular) > span:before {
    content: url("data:image/svg+xml;base64,");
    margin-right: 4px;
    padding-top: 4px;
}

.c-dark .dropdown:not(.regular) > span:before {
    content: url("data:image/svg+xml;base64,");
}

.dropdown.regular span::before {
    display: none !important;
}

.dropdown ul {
    box-shadow: 0px 4px 24px rgba(91, 117, 203, 0.26);
    @apply absolute top-[56px] left-1/2 flex flex-col opacity-0 invisible  bg-white w-[220px] overflow-hidden rounded-xl py-2 -md:left-auto -md:right-0 -sm:translate-x-0 -sm:w-[200px];
    -webkit-transform-origin: 0 0;
        -ms-transform-origin: 0 0;
            transform-origin: 0 0;
    -webkit-transition: opacity 0.2s ease, visibility 0.2s ease, -webkit-transform 0.3s cubic-bezier(0.4, 0.6, 0.5, 1.32);
    transition: opacity 0.2s ease, visibility 0.2s ease, -webkit-transform 0.3s cubic-bezier(0.4, 0.6, 0.5, 1.32);
    -o-transition: opacity 0.2s ease, visibility 0.2s ease, transform 0.3s cubic-bezier(0.4, 0.6, 0.5, 1.32);
    transition: opacity 0.2s ease, visibility 0.2s ease, transform 0.3s cubic-bezier(0.4, 0.6, 0.5, 1.32);
    transition: opacity 0.2s ease, visibility 0.2s ease, transform 0.3s cubic-bezier(0.4, 0.6, 0.5, 1.32), -webkit-transform 0.3s cubic-bezier(0.4, 0.6, 0.5, 1.32);
    -webkit-transform: scale(0.8) translate(0, 4px) translateX(-50%);
        -ms-transform: scale(0.8) translate(0, 4px) translateX(-50%);
            transform: scale(0.8) translate(0, 4px) translateX(-50%);
}

.dropdown ul li {
    opacity: 0;
    -webkit-transform: translate(6px, 0);
    -ms-transform: translate(6px, 0);
    transform: translate(6px, 0);
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.dropdown ul li:hover {
    @apply bg-[#F1F9FF]
}

.dropdown ul li a {
